@media (max-width:917px) {
    .wrapper ul {
        text-align: center;
        display:-webkit-flex;
        display:-ms-flexbox;
        display:flex;
        flex-direction: column;
        justify-content: center;
        width: 125%;
    }

    .pic-search {
        margin-top:80px
    }

    .community,
    .website {
        margin-left:115px;
    }

    .community p,
    .website p {
        max-height:10px;
    }

    .website {
        margin-bottom:100px;
    }

    .community {
        margin-bottom:60px;
    }

    .wrapper ul li {
        margin-top:17px;
        margin-left:75px
    }

    .element {
        margin-top:40px;
    }

    .wrapper {
        max-width:774px;
    }

    .main-block .wrapper {
        max-width:730px;
    }

    .pic-logo {
        padding-right: 10px;
    }

    .pic-search {
        top:103px;
    }

    .out-block {
        height:auto;
    }

    .block-week-month-date,
    .time {
        height:auto;
    }
}

@media (max-width:773px) {
    .main-block .wrapper {
        max-width:800px;
    }
}

@media (max-width:584px) {
    .block-week-month-date,
    .time {
        height:auto;
    }


    footer .wrapper {
        text-align: center;
        display:-webkit-flex;
        display:-ms-flexbox;
        display:flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
    }

    .block-week-month-date {
        height:auto;
        width:40%;
        margin-bottom:25px;
        margin-left:35px;
    }

    .block-week-month-date button {
        display:inline-block;
        margin-top:10px;
    }

    .time p {
        width:auto;
        height:auto;
        text-align:center;
    }

    .time {
        margin-left:20px;
    }

    .clock-pic,
    .location-pic {
        opacity:0;
    }

    .event-time {
        padding-left:1px;
    }

    .time {
        padding-top:7px;
    }
}

@media (max-width:452px) {
    footer {
        width:100%;
    }
    .community .social-vk {
        position:absolute;
        height:2px;
        width:2px;
        margin: 17px -115px;
    }

    footer p {
        font-size:13px;
    }

    .website .internet-pic {
        position:absolute;
        height:1px;
        width:1px;
        margin: 17px -115px;
    }

    .community p {
        margin-right: 10px;
    }

    .wrapper ul li {
        margin-left:110px;
    }
}

@media (max-width:373px) {
    .main-block .wrapper {
        min-width:350px;
    }

    .time {
        margin-right:10px;
    }

    .block-week-month-date {
        margin-right:50px;
    }

    footer, header {
        min-width:350px;
    }
}
